import React, { useMemo } from 'react';
import Calculator from '../../components/Calculator';
import MedCalculator from '../../components/MedCalculator';
import logo from '../../assets/logo.png';
import logomed from '../../assets/logomed.png';

import Footer from '../../components/Footer';

import { useLocation } from 'react-router-dom';

// styles
import { Container, Logo } from './styles';

function usePage() {
  const { search } = useLocation();

  return useMemo(() => {
    const query = new URLSearchParams(search);
    return query.get('page') || 'saga';
  }, [search]);
}

const Home = () => {
  const page = usePage();

  const isSagaPage = useMemo(() => page === 'saga', [page]);

  return (
    <Container>
      <Logo src={isSagaPage ? logo : logomed} />
      {isSagaPage ? <Calculator /> : <MedCalculator />}
      <Footer page={page} />
    </Container>
  );
};

export default Home;
