import React from 'react';
import { Link } from 'react-router-dom';

// styles
import { Container, LinkWrapper } from './styles';

const Footer = ({ page = 'saga' }) => {
  return (
    <Container>
      <LinkWrapper selected={page === 'saga'}>
        <Link to="/?page=saga"> Saga Tributária </Link>
      </LinkWrapper>
      {'<>'}
      <LinkWrapper selected={page !== 'saga'}>
        <Link to="/?page=med"> MedTributária </Link>
      </LinkWrapper>
    </Container>
  );
};

export default Footer;
