import React, { useCallback, useState } from 'react';

import MedModal from '../MedModal';

// styles
import {
  Container,
  Input,
  InputLabel,
  InputWrapper,
  Wrapper,
  Button,
  InputIcon,
} from './styles';

const Calculator = () => {
  const [pfValue, setpfValue] = useState(0);
  const [pjValue, setpjValue] = useState(0);

  const [pfInput, setpfInput] = useState('');
  const [pjInput, setpjInput] = useState('');

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');

  const handleOpenLink = useCallback(() => {
    const url =
      'https://api.whatsapp.com/send?phone=5532991215205&text=Olá!%20Quero%20restituir%20os%20impostos%20com%20a%20medtributária.';
    window.open(url, '_blank').focus();
  }, []);

  const handleOpenModal = useCallback(() => {
    if (pfValue === 0 && pjValue === 0) {
      setErrorMessage(
        'Por favor, informe um faturamento mensal PF e/ou PJ válido!',
      );
    }
    setIsModalOpen(true);
  }, [pfValue, pjValue]);

  const handleCloseModal = useCallback(() => {
    setIsModalOpen(false);
    setErrorMessage('');
  }, []);

  const handleChangeInput = useCallback((value, type) => {
    const onlyDigits = value
      .split('')
      .filter(s => /\d/.test(s))
      .join('')
      .padStart(3, '0');

    const monthlyAmount = parseFloat(onlyDigits) / 100;

    const digitsFloat = onlyDigits.slice(0, -2) + '.' + onlyDigits.slice(-2);
    const valueFormatted = new Intl.NumberFormat('pt-BR', {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    }).format(digitsFloat);

    if (type === 'pf') {
      setpfInput(valueFormatted);
      setpfValue(monthlyAmount);
    } else {
      setpjInput(valueFormatted);
      setpjValue(monthlyAmount);
    }
  }, []);

  return (
    <Container>
      <Wrapper>
        <InputLabel>INFORME SEU FATURAMENTO MENSAL </InputLabel>
        <InputLabel>(Pessoa Física)</InputLabel>
        <InputWrapper>
          <InputIcon>R$</InputIcon>
          <Input
            value={pfInput}
            onChange={e => handleChangeInput(e.target.value, 'pf')}
            type="text"
            placeholder="10.000,00"
          />
        </InputWrapper>
      </Wrapper>

      <Wrapper>
        <InputLabel>INFORME SEU FATURAMENTO MENSAL</InputLabel>
        <InputLabel>(Pessoa Jurídica)</InputLabel>
        <InputWrapper>
          <InputIcon>R$</InputIcon>
          <Input
            value={pjInput}
            onChange={e => handleChangeInput(e.target.value, 'pj')}
            type="text"
            placeholder="10.000,00"
          />
        </InputWrapper>
      </Wrapper>
      {!isModalOpen && (
        <Wrapper>
          <Button onClick={handleOpenModal}>CALCULAR SEU CRÉDITO!</Button>
          <Button onClick={handleOpenLink}>QUERO RESTITUIR AGORA!</Button>
        </Wrapper>
      )}
      <MedModal
        pfValue={pfValue}
        pjValue={pjValue}
        isOpen={isModalOpen}
        errorMessage={errorMessage}
        handleCloseModal={handleCloseModal}
        handleOpenLink={handleOpenLink}
      />
    </Container>
  );
};

export default Calculator;
