import React, { useEffect, useState, useMemo } from 'react';

// styles
import {
  Container,
  ModalHeader,
  ModalContent,
  Overlay,
  Divider,
  Button,
  Message,
  Disclaimer,
  Value,
  CloseIcon,
  Title,
  Wrapper,
} from './styles';

const MedModal = ({
  isOpen,
  errorMessage,
  handleCloseModal,
  handleOpenLink,
  pfValue,
  pjValue,
}) => {
  const [restitutionPFValue, setRestitutionPFValue] = useState(0);
  const [restitutionPJValue, setRestitutionPJValue] = useState(0);

  useEffect(() => {
    const inssMaxValue = 876.97;

    let pfTAX = 0;
    let pfAuxTAX = 0;
    let pjTAX = 0;

    if (pfValue >= 7507.49) {
      pfTAX = 0.14;
      pfAuxTAX = 174.08;
    }

    if (pjValue >= 7507.49) {
      pjTAX = 0.11;
    }

    const pfINSS = pfValue * pfTAX - pfAuxTAX;
    const pjINSS = pjValue * pjTAX;

    const pfRestitution = pfINSS - (pfINSS > 0 ? inssMaxValue : 0);
    const pjRestitution = pjINSS - (pjINSS > 0 ? inssMaxValue : 0);

    setRestitutionPFValue(pfRestitution);
    setRestitutionPJValue(pjRestitution);
  }, [pfValue, pjValue]);

  const error = useMemo(() => errorMessage !== '', [errorMessage]);

  return (
    <Overlay
      onClick={handleCloseModal}
      style={{ display: isOpen ? 'inherit' : 'none' }}
    >
      <Container>
        <ModalHeader>
          <CloseIcon>x</CloseIcon>
        </ModalHeader>
        <Divider width="100%" />
        {error ? (
          <ModalContent>
            {errorMessage.split('\n').map((message, index) => (
              <>
                <Message key={`error-message-${index}`}>{message}</Message>
                <br />
              </>
            ))}
          </ModalContent>
        ) : (
          <ModalContent>
            {pfValue !== 0 && (
              <Wrapper>
                <Title>Pessoa Física</Title>
                <Message>
                  Economia mensal:{' '}
                  <Value>
                    R${' '}
                    {restitutionPFValue.toLocaleString('pt-br', {
                      minimumFractionDigits: 2,
                      maximumFractionDigits: 2,
                    })}
                  </Value>
                </Message>
                <Message>
                  Em 60 meses:{' '}
                  <Value>
                    R${' '}
                    {(restitutionPFValue * 60).toLocaleString('pt-br', {
                      minimumFractionDigits: 2,
                      maximumFractionDigits: 2,
                    })}
                  </Value>
                </Message>
              </Wrapper>
            )}

            {pjValue !== 0 && (
              <Wrapper>
                <Title>Pessoa Jurídica</Title>
                <Message>
                  Economia mensal:{' '}
                  <Value>
                    R${' '}
                    {restitutionPJValue.toLocaleString('pt-br', {
                      minimumFractionDigits: 2,
                      maximumFractionDigits: 2,
                    })}
                  </Value>
                </Message>
                <Message>
                  Em 60 meses:{' '}
                  <Value>
                    R${' '}
                    {(restitutionPJValue * 60).toLocaleString('pt-br', {
                      minimumFractionDigits: 2,
                      maximumFractionDigits: 2,
                    })}
                  </Value>
                </Message>
              </Wrapper>
            )}

            {pfValue !== 0 && pjValue !== 0 && <Divider width="20%" />}
            {pjValue !== 0 && (
              <Wrapper>
                <Title>Economia Total</Title>
                <Message>
                  Restituição mensal:{' '}
                  <Value>
                    R${' '}
                    {(restitutionPFValue + restitutionPJValue).toLocaleString(
                      'pt-br',
                      {
                        minimumFractionDigits: 2,
                        maximumFractionDigits: 2,
                      },
                    )}
                  </Value>
                </Message>
                <Message>
                  Em 60 meses:{' '}
                  <Value>
                    R${' '}
                    {(
                      restitutionPFValue * 60 +
                      restitutionPJValue * 60
                    ).toLocaleString('pt-br', {
                      minimumFractionDigits: 2,
                      maximumFractionDigits: 2,
                    })}
                  </Value>
                </Message>
              </Wrapper>
            )}

            <Wrapper>
              <Disclaimer>
                Obs: esse é um cálculo aproximado. Simulação de crédito a
                depender da sua contabilidade atual.
              </Disclaimer>
            </Wrapper>
          </ModalContent>
        )}

        {error || (!restitutionPFValue && !restitutionPJValue) ? (
          <Button onClick={handleOpenLink}>ENTRAR EM CONTATO AGORA</Button>
        ) : (
          <Button onClick={handleOpenLink}>RESTITUIR AGORA</Button>
        )}
        <Button onClick={handleCloseModal}>CALCULAR NOVAMENTE</Button>
      </Container>
    </Overlay>
  );
};

export default MedModal;
